body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background: #ffffff;
}


.form-wrapper {
  background-image: url(/My-PortFolio/static/media/backgroundimg.7164f7ba.jpg);
  background-repeat : no-repeat;
  background-size : cover;
  width: 100vw;
  height: 100vh;
  padding: 1rem;
  justify-content: center;
  padding: 2rem;
  font-size: 3.5rem;
  text-align: center;
  font-weight: bold;
  color: white;  
  display: flex;
  align-items: center;
}

.Intro {
  width: 100vw;
  height: 100vh;
  padding: 2rem;
  font-family: 'nanumsquare',sans-serif;
  font-size: 4rem;
  text-align: center;
  font-weight: 900;
  color: white;  
  display: flex;
  align-items: center;
  background: #4d8596;
  padding: 1rem;
  justify-content: center;
  display: flex;
  }

.myInfo {
  width: 100vw;
  height: 100vh;
  padding: 2rem;
  font-size: 3.5rem;
  text-align: center;
  font-weight: bold;
  color: rgb(47, 56, 55);  
  display: flex;
  align-items: center;
  background: #91fada;
  padding: 1rem;
  justify-content: center;
  display: flex;
  }
  

  
.skill {
  width: 100vw;
  height: 100vh;
  padding: 2rem;
  font-size: 3.5rem;
  text-align: center;
  font-weight: bold;
  color: rgb(47, 50, 56);  
  display: flex;
  align-items: center;
  background: #ffa3a3;
  padding: 1rem;
  justify-content: center;
  display: flex;
  }
  

.projects {
  padding: 2rem;
  text-align: center;
  display: flex;
  align-items: center;
  width: 100vw;
  height: 100vh;
  padding: 1rem;
  justify-content: center;
  background-color: rgb(255, 255, 255);
  display: grid;
}

.prj_title {
  color: rgb(48, 46, 46);  
  font-size: 3rem;
  padding: 3rem;
  font-weight: 900;
  /* font-family: 'Roboto', sans-serif; */
  font-family: 'nanumsquare', sans-serif;
  padding-bottom: 15%;
}

.infoForm{
  display: flex;
  justify-content: center;
  padding: 1rem;
  width: 95%;
  margin-bottom: 50px;
}
img {
  height: 30%;
  width: 30%;
  border: 1px solid gainsboro;
  
}


/* 프로젝트 설명창 */
.infoText{
  /* padding-left: 5%; */
  width: 60%;

}

/* 년도 */
.yearDiv{
  font-family: 'Roboto', sans-serif;
  text-align: left;
  font-size: 1.7rem;
  font-weight: bold;
  color: gray;
  padding-left: 5%;
  padding-bottom: 10%;
}

/* 프로젝트 명 */
.prjDiv{
  text-align: center;
  font-size: 1.5rem;
  font-weight: bolder;
  font-family: 'nanumsquare', sans-serif;
  color: rgb(90, 90, 90);
  padding-bottom: 2%;
}

/* 프로젝트내용 */
.contetDiv{
  font-family: 'nanumsquare', sans-serif;
  font-weight: 500;
  text-align: center;
  font-size: 0.9rem;
}


/* 버튼 */
.btnDiv{
  margin-top: 10%;
}
.btnDiv>a{
  color: white;
  text-decoration: none;
  outline:0; 
  font-size:1em;
  font-weight:600;
  background:rgb(255, 198, 76);
  border:none;
  padding:0.5rem 0.5em 0.8rem 0.5em;
  margin: 7px;
  transition:all .3s ease-out; 
  box-shadow:inset 0 -8px 0 0 rgba(0,0,0,.2),
    1px 1px 0 0 #ab81c7,
    2px 2px 0 0 #ab81c7,
    3px 3px 0 0 #ab81c7,
    4px 4px 0 0 #ab81c7,
    5px 5px 0 0 #ab81c7,
    6px 6px 0 0 #ab81c7,
    7px 7px 0 0 #ab81c7,
    8px 8px 0 0 #ab81c7,
    9px 9px 0 0 #ab81c7;
    /* 
    
    font-size: 1rem;
    display : inline-block;
    border-radius: 5px;
    padding: 7px 4px 7px 4px;
    transition: all 0.1s;
    
     */
    width: 60px;
    font-family: 'Roboto', sans-serif;
}

.btnDiv>a:hover{
  color:#444;
}
.btnDiv>a:active{
  color:#222;
  box-shadow:inset 0 -4px 0 0 rgba(0,0,0,.2),
    1px 1px 0 0 #d98e20,
    2px 2px 0 0 #d98e20,
    3px 3px 0 0 #d98e20,
    4px 4px 0 0 #d98e20,
    5px 5px 0 0 #d98e20;
}

